import React from 'react';
import cx from 'classnames';

const CustomCardTitle = ({image, reveal, waves, children, s, ...props}) => {
    const classes = cx({
        'card-image': true,
        'waves-effect': waves,
        'waves-block': waves,
        [`waves-${waves}`]: waves,
    });

    return (
        <div className={cx(classes)} {...props} >
            <div style={{
                "height": "0",
                "padding-top": "100%",
                "position": "relative",
                "display": 'block',
            }}>
                <img className={cx({activator: reveal})} src={image}
                     style={{
                         "position": "absolute",
                         "top": "50%",
                         "left": "50%",
                         "transform": "translate(-50%, -50%)",
                         "max-height": "100%",
                         "max-width": "100%",
                         "width": "auto"
                     }}/>
            </div>
            <span className={cx('card-title')}>{children}</span>
        </div>
    );
};

export default CustomCardTitle;
